<template>
	<view>
		
		<!-- <uni-nav-bar :border="false" @clickLeft="back" left-icon="back"  title="考试详情"></uni-nav-bar> -->
		
		<!-- 图片 -->
		<view class="kaoshi-top-wrap">
			<image class="kaoshi-info-img" :src="detailInfo.thumb" mode="aspectFill"></image>
		</view>
		
		<!-- 标题介绍价格 包含课程 -->
		<view class="kaoshi-md-wrap">
			<view class="kaoshi-info-title">{{detailInfo.title}}</view>
			<view class="kaoshi-info-introduce">{{detailInfo.des}}</view>
			<view class="kaoshi-info-article">
				<view class="kinfo-article-item">
					<view class="kinfo-article-left">
						<image class="kinfo-img-item" src="../../static/images/kaoshi_time.png" mode="aspectFill"></image>
						<text v-if="detailInfo.list != undefined">{{detailInfo.list[0].text}}</text>
					</view>
					<view class="kinfo-article-right">
						<image class="kinfo-img-item" src="../../static/images/kaoshi_shichang.png" mode="aspectFill"></image>
						<text v-if="detailInfo.list != undefined">{{detailInfo.list[1].text}}</text>
					</view>
				</view>
				
				<view class="kinfo-article-item">
					<view class="kinfo-article-left">
						<image class="kinfo-img-item" src="../../static/images/kaoshi_timu.png" mode="aspectFill"></image>
						<text v-if="detailInfo.list != undefined">{{detailInfo.list[2].text}}</text>
					</view>
					<view class="kinfo-article-right">
						<image class="kinfo-img-item" src="../../static/images/kaoshi_fenshu.png" mode="aspectFill"></image>
						<text v-if="detailInfo.list != undefined">{{detailInfo.list[3].text}}</text>
					</view>
				</view>
				
				<view class="kinfo-article-item">
					<view class="kinfo-article-left">
						<image class="kinfo-img-item" src="../../static/images/kaoshi_fenlei.png" mode="aspectFill"></image>
						<text v-if="detailInfo.list != undefined">{{detailInfo.list[4].text}}</text>
					</view>
					<view class="kinfo-article-right">
						<image class="kinfo-img-item" src="../../static/images/kaoshi_ren_num.png" mode="aspectFill"></image>
						<text v-if="detailInfo.list != undefined">{{detailInfo.list[5].text}}</text>
					</view>
				</view>
				
				
			</view>
			
			<view class="fat-border"></view>
		
			<!-- 必修课程 -->
			<view v-if="comInfo.length > 0" class="bixiu-course-wrap">
				<text class="kaoshi-contain-title">必修课程</text>
				<view class="course-wrap">
					<view @click="viewCourseInfo(item.id, item.sort)" class="live-list" v-for="(item, index) in comInfo"  :key="index">
						<view class="live-list-img-wrap">
							<image class="live-list-img" :src="item.thumb" mode="aspectFit"></image>
							<text class="course-title-icon">{{typeName[item.sort]}}</text>
							
						</view>
						
						<view class="live-list-info">
							<view class="live-c-title">{{item.name}}</view>
							<view class="live-status">
								{{item.lesson}}
							</view>
							<view class="live-teacher-price">
								<image class="live-teacher-avatar" :src="item.avatar" mode=""></image>
								<text class="teacher-name">{{item.user_nickname}}</text>
								<view class="price-wrap" :class="{numPrice : (item.payval != '免费')}">
									<text v-if="item.payval != '免费' && item.payval != ''">￥</text>
									<text class="live-price">{{item.payval}}</text>
								</view>
							</view>
						</view>
					</view>
				</view>
				<view class="fat-border"></view>
			</view>
			
			
			
			<!-- 推荐课程 -->
			<view v-if="recomInfo.length > 0" class="recom-course-wrap">
				<text class="kaoshi-contain-title">推荐课程</text>
				<view class="course-wrap">
					<view @click="viewCourseInfo(item.id, item.sort)" class="live-list" v-for="(item, index) in recomInfo"  :key="index">
						<view class="live-list-img-wrap">
							<image class="live-list-img" :src="item.thumb" mode="aspectFit"></image>
							<text class="course-title-icon">{{typeName[item.sort]}}</text>
						</view>
						
						<view class="live-list-info">
							<view class="live-c-title">{{item.name}}</view>
							<view class="live-status">
								{{item.lesson}}
							</view>
							<view class="live-teacher-price">
								<image class="live-teacher-avatar" :src="item.avatar" mode=""></image>
								<text class="teacher-name">{{item.user_nickname}}</text>
								<view class="price-wrap" :class="{numPrice : (item.payval != '免费')}">
									<text v-if="item.payval != '免费' && item.payval != ''">￥</text>
									<text class="live-price">{{item.payval}}</text>
								</view>
							</view>
						</view>
					</view>
				</view>
				<view class="fat-border"></view>
			</view>
			
			
		</view>
		
		
		
			<!-- 跳出弹窗 -->
			<!-- 0未开始 1待做 2未付费 3已做完 4继续答题 5已过期 -->
			
		<view v-if="btn_type == '0'" class="begin-kaoshi-btn ban-click">考试未开始</view>
		<view v-else-if="btn_type == '1'" class="begin-kaoshi-btn" @click="openKaoshiInfo">开始考试</view>
		<view v-else-if="btn_type == '2'" class="begin-kaoshi-btn" @click="openKaoshiInfo">开始考试</view>
		<view v-else-if="btn_type == '3'" class="begin-kaoshi-btn" @click="viewResult">查看成绩</view>
		<view v-else-if="btn_type == '4'" class="begin-kaoshi-btn" @click="goOnKaoshiInfo">继续答题</view>
		<view v-else-if="btn_type == '5'" class="begin-kaoshi-btn ban-click">考试已结束</view>
			
	
		
		
		
	</view>
</template>

<script>
	
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
	const app = getApp();
	
	export default {
		components:{
			uniNavBar
		},
		data() {
			return {
				detailInfo: {}, //全部
				comInfo: [], //必修课程
				recomInfo: [], //推荐课程
				typeName: {
					// 0内容1课程2语音直播3视频直播 
					'0' : '内容',
					'1' : '课程',
					'2' : '语音直播',
					'3' : '视频直播'
				},
				btn_type: '', // 0开始考试 1 继续考试 2查看成绩 3考试未开始 4考试已结束
				userinfo: '', //用户信息
				testsid: '', //试题id
				mytests: [] ,//我的考试列表
				isCanKaoshi: '0', //是否可以考试
			}
		},
		onLoad(option) {
			this.userinfo = uni.getStorageSync('userinfo');
			if(this.userinfo == '') {
				uni.navigateTo({
					url: '../login/login',
				});
				return;
			}
			let tid = option.tid;
			this.getMyKaoshi(tid); //获取我的考试
			this.getKaoshiDetail(tid); //获取题目详情
			this.testsid = tid;
		},
		methods: {
			
			// 查看成绩
			viewResult() {
				uni.navigateTo({
					url: '../kaoshi_result/kaoshi_result?testsid=' + this.testsid,
				});
			},	
			// 判断试题是否已经做过
			getMyKaoshi(tid) {
				let gData = app.globalData;
				
				let that = this;
				uni.request({
					url: app.globalData.site_url + 'Tests.GetMyTests',
					method: 'GET',
					data: {
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token,
					},
					success: res => {
						console.log(res);
						that.mytests = res.data.data.info;
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});	
				
			},
			// 检测考试
			checkKaoshi(tid) {
				let gData = app.globalData;
				let that = this;
				uni.request({
					url: gData.site_url + 'Tests.Check',
					method: 'GET',
					data: {
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token,
						'testsid' : tid
					},
					success: res => {
						console.log(res);
						if(res.data.data.code != '0') {
							uni.showModal({
								showCancel: false,
								content: res.data.data.msg,
								confirmText: '我知道了',
								confirmColor: '#38DAA6',
								success: res => {
									
								},
								fail: () => {},
							});
							return;
						}
						that.isCanKaoshi = '1';
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			// 开始考试
			openKaoshiInfo() {
				this.checkKaoshi(this.testsid);	
				if(this.isCanKaoshi == '1') {
					uni.navigateTo({
						url: '../kaoshi_info/kaoshi_info?testsid=' + this.detailInfo.id + '&type=' + '0',
					});
				}
				
			},
			// 继续答题
			goOnKaoshiInfo() {
				uni.navigateTo({
					url: '../kaoshi_info/kaoshi_info?testsid=' + this.detailInfo.id + '&type=' + '1',
				});
			},
			// 获取考试试题详情
			getKaoshiDetail(tid) {
				let gData = app.globalData;
				uni.request({
					url: gData.site_url + 'Tests.GetTests',
					method: 'GET',
					data: {
						// uid token testsid 测试用 先不用token
						'token': this.userinfo.token,
						'uid' : this.userinfo.id,
						'testsid' : tid,
					},
					success: res => {
						console.log(res);
						if(res.data.data.code != '0') {
							return;
						}
						
						if(res.data.data.info != undefined) {
							this.detailInfo = res.data.data.info[0];
							this.comInfo = res.data.data.info[0].com;
							this.recomInfo = res.data.data.info[0].recom;
							this.btn_type = res.data.data.info[0].status;
						} else {
							this.detailInfo = [];
							this.comInfo = [];
							this.recomInfo = [];
							this.btn_type = '';
						}
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			// 查看大班课(语音、PPT、视频直播)内容详情
			viewLiveInfo(liveCourseId){
				console.log(333);
				uni.navigateTo({
					url: '../../packageB/pages/live_course_info/live_course_info?courseid=' + liveCourseId,
				});
			},
			viewContentInfo(contentCourseId) {
				uni.navigateTo({
					url: '../content-info/content-info?courseid=' + contentCourseId,
				});
			},
			viewCourseInfo(cid, csort) {
				console.log(csort);
				if(csort == '0') {
					this.viewContentInfo(cid);
				} else {
					this.viewLiveInfo(cid);
				}
				
			},
			back() {
				uni.navigateBack({
					delta: 1
				});
			}
		}
	}
</script>

<style>
	@import url("/static/css/course_list.css");
	
	.kaoshi-info-img {
		width: 100%;
	}
	
	/* 中间包装 */
	.kaoshi-md-wrap {
		padding: 0 20rpx;
		margin-bottom: 80rpx;
	}
	
	.kaoshi-info-title {
		font-weight: bold;
		font-size: 32rpx;
		margin-bottom: 20rpx;
	}
	
	.kaoshi-info-introduce {
		color: #969696;
		font-size: 20rpx;
		margin-bottom: 20rpx;
		padding-bottom: 16rpx;
		border-bottom: 2rpx solid #F0F0F0;
	}
	
	.kaoshi-info-article {
		margin-bottom: 15rpx;	
		padding-bottom: 5rpx;
	}
	
	.fat-border {
		height: 10rpx;
		margin-top: 10rpx;
		margin-bottom: 20rpx;
		background-color: #F5F5F5;
	}
	
	.kinfo-article-item {
		display: flex;
		margin-bottom: 18rpx;
	}
	
	.kinfo-article-item text {
		color: #969696;
		font-size: 20rpx;
	}
	
	.kinfo-article-left, 
	.kinfo-article-right {
		width: 60%;
		display: flex;
		align-items: center;
	}
	
	.kinfo-article-right {
		width: 40%;
	}
	
	.kinfo-al-title {
		margin-right: 10rpx;
	}
	
	.kinfo-img-item {
		width: 30rpx;
		height: 30rpx;
		margin-right: 15rpx;
	}
	
	/*必修课程*/
	.kaoshi-contain-title {
		display: inline-block;
		font-size: 32rpx;
		font-weight: bold;
		margin-bottom: 20rpx;
	}
	
	.recom-course-wrap {
		margin-bottom: 100rpx;
	}
	

	.begin-kaoshi-btn {
		height: 80rpx;
		line-height: 80rpx;
		width: 94%;
		border-radius: 7rpx;
		text-align: center;
		background-color: #38DAA6;
		color: #FFFFFF;
		font-size: 30rpx;
		position: fixed;
		bottom: 0rpx;
		left: 50%;
	    -webkit-transform: translateX(-50%);
		transform: translateX(-50%);
	}
	
	.ban-click {
		background-color: #D2D2D2;
		color: #FFFFFF;
	}
	
	

</style>
